@import url(https://cdn.syncfusion.com/ej2/material.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: "AppleSDGothicNeoR";
    font-weight: 400;
    src: url(/static/media/AppleSDGothicNeoR.2c8eadf7.eot);
    src: url(/static/media/AppleSDGothicNeoR.02584433.woff) format("woff");
}

@font-face {
    font-family: "AppleSDGothicNeoM";
    font-weight: 500;
    src: url(/static/media/AppleSDGothicNeoM.c1f6e829.eot);
    src: url(/static/media/AppleSDGothicNeoM.25422d84.woff) format("woff");
}

@font-face {
    font-family: "AppleSDGothicNeoSB";
    font-weight: 600;
    src: url(/static/media/AppleSDGothicNeoSB.d1f5bc8b.eot);
    src: url(/static/media/AppleSDGothicNeoSB.1b08ca06.woff) format("woff");
}

@font-face {
    font-family: "AppleSDGothicNeoB";
    font-weight: 700;
    src: url(/static/media/AppleSDGothicNeoB.e1a18d4c.eot);
    src: url(/static/media/AppleSDGothicNeoB.f30f14ee.woff) format("woff");
}
* {
    margin: 0;
    padding: 0;
    word-break: keep-all;
}

ul,
li {
    list-style: none;
}

img {
    display: block;
    float: left;
}

a {
    text-decoration: none;
    color: #333333;
}

.wrap {
    margin: 0 auto;
    width: 100%;
    max-width: 375px;
    overflow: hidden;
}

header,
section,
footer {
    float: left;
    width: 100%;
    overflow: hidden;
}

footer{
    margin-bottom: 20px;
    align-self: flex-end;
}

footer p{
    text-align: center;
    font-family: "AppleSDGothicNeoM";
    color: #3A4759;
}

.none{
    display: none;
}

.center_wrap{
    display: flex;
    height: 90vh;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    position: relative;
}
