.mypage{
    width: 100%;

    header,.section_1{
        width: 90%;
        margin: 20px auto 0  auto;
    }

    .section_1{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 135px;

        figure{
            cursor: pointer;
        }

        p,h3{
            font-size: 25px;
            line-height: 1.25;
            font-family: "AppleSDGothicNeosB";
            color: #3A4759;
        }

        .myName b{
            color: #49A5F3;
        }

        span{
            color: #999A9B;
            font-size: 10px;
            font-family: "AppleSDGothicNeosB";
            position: relative;
            top: -3px;
        }
    }

    .section_2{
        height: 407px;

        .tab_menu{
            display: flex;
            flex-direction: column;
        }

        .tab_menu li{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            height: 60px;
            border-top: 1px solid #dcdcdc;
            box-sizing: border-box;
            padding:0 15px;
            font-family: "AppleSDGothicNeoB";
            color: #3A4759;
            font-size: 16px;
        }

        .tab_menu li:last-child{
            border-bottom: 1px solid #dcdcdc;
            box-sizing: border-box;
        }

        .tab_menu li a{
            display: flex;
            justify-content: space-between;
            width: 100%;
            font-family: "AppleSDGothicNeoB";
            color: #3A4759;
            font-size: 16px;
        }
    }
}
