.calendar {

    .important_data {
        display: flex;
        flex-direction: column;
    }

    .e-calendar {
        border: 0;
        max-width: 100%;
        width: 100%;
        overflow: hidden;
        margin-top:12px;
        border-bottom: 1px solid #ddd;
        box-sizing: border-box;
    }

    .e-calendar.e-content td.e-focused-date.e-today span.e-day {
        border: 0;
        color: #fff;
        background: #F7C667;
    }

    .e-calendar .e-content td.e-selected span.e-day{
        color: #fff;
        background: #F7C667  !important;
        border: 0;
    }

    .e-calendar .e-content td.e-today:hover span.e-da{
        border: 0;
    }

    .e-calendar .e-content td.e-focused-date span.e-day,
    .e-calendar .e-content td.e-focused-date:hover span.e-day,
    .e-calendar .e-content td.e-focused-date:focus span.e-day,
    .e-bigger.e-small .e-calendar .e-content td.e-focused-date span.e-day,
    .e-bigger.e-small .e-calendar .e-content td.e-focused-date:hover span.e-day,
    .e-bigger.e-small .e-calendar .e-content td.e-focused-date:focus span.e-day {
        color: #fff;
        background: #F7C667;
        border: 0;
    }

    .e-calendar .e-content td.e-today span.e-day{
        border: 0;
        color: #fff;
        background: #8cc6f6;
    }

    .e-calendar th {
        font-family: "AppleSDGothicNeoB";
    }

    .e-day {
        font-family: "AppleSDGothicNeoR";
    }

    .e-title {
        font-size: 24px !important;
        font-family: "AppleSDGothicNeoSB";
        color: #f5aa19 !important;
    }

    .e-footer-container {
        display: none !important;
    }

    .title_wrap {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        padding: 0 10px;
        box-sizing: border-box;
    }

    .date_title {
        font-size: 18px;
        font-family: "AppleSDGothicNeoSB";
        color:#333;
        margin:15px 0;
    }

    .all_view {
        font-size: 12px;
        color: #808080;
    }

    .date_wrap {
        display: flex;
        width: 100%;
        height: 83px;
        margin-bottom: 12px;
        justify-content: space-between;
        align-items: center;
        border: 0.6px solid #E2E2E2;
        box-sizing: border-box;
        border-radius: 30px;
        padding: 0 25px;

        h5 {
            font-size: 16px;
            color: #383838;
            font-family: "AppleSDGothicNeoR";
        }

        p {
            font-family: "AppleSDGothicNeoR";
            font-size: 12px;
            color: #B1B1B1;
            margin-top: 5px;
        }
    }

    .footer_bar button {
        display: none;
    }
}
